
html {
  height: 100%;
}
body {
  height: 100%;
}
#app-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  margin: 0 15vw;
}
#app .connecting {
  margin-top: 150px;
  text-align: center;
  font-size: 48px;
}
#nav {
	width: 100%;
  margin-bottom: 50px;
}
#nav .pure-menu-link {
  padding: 20px 20px;
}
#app {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
}
.debug.inspect {
  color: #ccc;
  display: none;
  cursor: pointer;
}
*:hover > .debug.inspect {
  display: inline;
}
.cards {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 10px;
  width: 360px;
}
.cards .card.expanded {
  min-width: 45%;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  min-height: 600px;
}
.card .card-row-expand {
  min-height: 200px;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.card .tile {
  padding: 15px;
}
.card .tile.fullsize {
  padding-left: 0px;
  padding-right: 0px;
}
.pure-menu-list.menu-flex {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
}
.menu-expand {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.menu-small {
  font-size: 12px;
}
.pure-table .table-collapse {
  border-bottom: 0;
}
.prop-object {
  width: 100%;
}
.prop-object th {
  white-space: nowrap;
  text-align: left;
}
.prop-object td {
  width: 100%;
}
.prop-object .prop-item-complex {
  padding-left: 40px;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.2s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.card textarea.fullsize {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  white-space: pre;
  overflow: auto;
  border: 0;
  padding: 15px;
}




