
#calendar-heatmap svg {
  overflow: visible;
}
.daily-report b {
  display: inline-block;
  width: 200px;
}
text.month-name,
text.calendar-heatmap-legend-text,
text.day-initial {
  font-size: 14px;
  font-family: Helvetica, arial, 'Open Sans', sans-serif;
  fill: #aaa;
}
rect.day-cell:hover {
  stroke: #555555;
  stroke-width: 1px;
}
.day-cell-tooltip {
  position: absolute;
  z-index: 9999;
  padding: 5px 9px;
  color: #bbbbbb;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.85);
  border-radius: 3px;
  text-align: center;
}
.day-cell-tooltip > span {
  font-family: Helvetica, arial, 'Open Sans', sans-serif
}
.calendar-heatmap {
  box-sizing: initial;
}
.badge {
  background: #5cc1df;
  border-radius: 10px;
  padding: 0px 6px;
  color: #000;
  margin-left: 5px;
  font-size: 0.9em;
}
.tag-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tag-bar li {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  list-style-type: none;
  padding: 0px 5px;
  font-weight: bold;
  font-size: 0.8em;
}



